





































import {Component, Prop, Vue} from 'vue-property-decorator';
import Wallet from '@/modules/wallet/models/wallet';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import Project from '@/modules/project/models/project';

@Component({})
export default class ProcessTransactionStep2 extends Vue {
  @Prop() value!: ExchangeTransaction;
  @Prop() wallet!: Wallet;
  @Prop() project!: Project;

  get total() {
    return this.value.tokenAmount * this.value.price;
  }

  goToNextAction() {
    this.$emit('next');
  }

  goBackAction() {
    this.$emit('back');
  }
}
