


































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import WalletRepository from '../../../repository/wallet-repository';
import Wallet from '../../../models/wallet';
import RULES from '../../../../../utils/rules';

@Component({})
export default class WithdrawPaypalScreen extends Vue {
  @Prop() wallet!: Wallet;
  amount: string = '0';
  loading: boolean = false;
  valid = true;
  errorMessage: string = '';
  error: boolean = false;

  get rules() {
    return {
      required: RULES.required,
      isNumber: RULES.isNumber,
    };
  }

  onAmoutInput() {
    this.errorMessage = '';
    this.error = false;
  }

  async withdrawAction() {
    const isValidated = (this.$refs.form as any).validate();
    if (isValidated) {
      this.loading = true;
      try {
        if (this.wallet.usd >= parseFloat(this.amount)) {
          const result = await WalletRepository.withdrawUsd(parseFloat(this.amount));
          if (result) {
            this.$router.replace('/wallet');
          }
        } else {
          this.error = true;
          this.errorMessage = 'Balance is not enough';
        }
      } catch (e) {
      } finally {
        this.loading = false;
      }
    }
  }
}
